<template>
  <div class="dialogue-info">
    <header id="wx-header">
      <!--<div class="other"><span>添加朋友</span></div>-->
      <div class="center">
        <div class="iconfont icon-return-arrow" v-on:click="$router.back()">
          <span>返回</span>
        </div>
        <span>选择联系人</span>
        <span style="position: absolute;right:15px;top:0;">
          <van-button type="primary" size="small" @click="confirm" v-show="!determine">确定 ({{result.length-1}})</van-button>
        </span>
        <span style="position: absolute;right:15px;top:0;" v-show="determine">
          <van-button type="default" size="small" @click="confirm" disabled>确定</van-button>
        </span>
      </div>
    </header>
    <!-- 选中好友头像 -->
    <van-row class="friendList">
      <div v-show="isShow" style="color:#999;margin:10px 0 20px 5px">选择联系人......</div>
      <van-col span="3" v-for="(item,index) in FriendHeaderUrl" :key="index">
        <div>
          <img :src="'/api/file/showImg?fileUrl='+item" alt class="imgStyle" />
        </div>
      </van-col>
    </van-row>
    <!-- 好友列表 -->
    <van-checkbox-group v-model="result" @change="change">
      <van-cell-group>
        <van-cell
          v-for="(item, index) in friendList"
          clickable
          :key="index"
          @click="toggle(index)"
          @change="change"
        >
          <img
            :src="'/api/file/showImg?fileUrl='+item.user.headerUrl"
            alt
            style="width:40px;height:40px;"
          />
          <span>{{item.user.nickname}}</span>
          <van-checkbox
            :name="item.user.userId"
            ref="checkboxes"
            slot="right-icon"
            :disabled="item.disabled"
          />
        </van-cell>
      </van-cell-group>
    </van-checkbox-group>
  </div>
</template>
<script>
import { getMessage, send } from "../../socket/socket";
import qs from "qs";
export default {
  data() {
    return {
      isShow: true,
      determine:true,//确定按钮禁选转换
      result: [this.$route.query.userId],
      friendList: [], //好友列表
      checkFriendList: [], //选中好友
      FriendHeaderUrl: [], //选中好友头像
      userInfo: "",
      myFriend: ""
    };
  },
  created() {
    this.userInfo = JSON.parse(window.localStorage.getItem("userInfo"));
    this.myFriend = this.$route.query.userId;
    this.getFriendList();
  },

  methods: {
    //好友列表
    getFriendList() {
      this.$http.get("/api/friend/getFriendList").then(res => {
        if (res.data.code == 200) {
          console.log(res);
          this.friendList = res.data.data;
          if (this.friendList) {
            this.friendList.map(item => {
              console.log(item);
              if (this.myFriend == item.user.userId) {
                item.disabled = true;
              } else {
                item.disabled = false;
              }
            });
            console.log(this.friendList);
          }
        }
      });
    },
    toggle(index) {
      if (this.friendList[index].disabled) {
        this.$refs.checkboxes[index].checked = true;
      } else {
        this.$refs.checkboxes[index].toggle();
      }
    },
    change(data) {
      console.log(data);
      this.select();
    },
    select() {
      //选择好友后头像列表
      this.checkFriendList = this.result.slice(1);
      this.FriendHeaderUrl = [];
      for (var i = 0; i < this.checkFriendList.length; i++) {
        for (var j = 0; j < this.friendList.length; j++) {
          if (this.checkFriendList[i] == this.friendList[j].user.userId) {
            this.FriendHeaderUrl.push(this.friendList[j].user.headerUrl);
          }
        }
      }
      console.log(this.checkFriendList);
      if (this.checkFriendList.length > 0) {
        this.isShow = false;
        this.determine=false;
      } else {
        this.isShow = true;
        this.determine=true;
      }
    },
    //确认拉人
    confirm() {
      console.log(this.result);
      let obj = this.result.join(",");
      let pullers = {
        pullers: obj
      };
      if (this.result.length > 1) {
        this.$http
          .post("/api/chat/creatGroupChatRoom", qs.stringify(pullers))
          .then(res => {
            // console.log(res);
            if (res.data.code == 200) {
              let params = {
                roomId: res.data.data.roomId,
                roomType: "group",
                userId: this.userInfo.userId,
                top: false,
                lastMsgId: null,
                limit: 2
              };
              this.$router.push({ path: "/wechat/dialogue", query: params });
            }
          });
      }
    }
  },
  mounted() {
    // console.log(this.$route.query);
  }
};
</script>
<style>
.dialogue-info {
  background: white;
}
.imgStyle {
  width: 40px;
  height: 40px;
  border-radius: 5px;
}
.friendList {
  background: white;
  padding: 10px;
  height: 66px;
}
</style>


